"use client";
//React and Next Import
import { Suspense, useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useRouter } from "nextjs-toploader/app";
//API
import { ResetPasswordApi } from "@/api/clientsideaction/profileapi";
//Utils
import {
  lowerPresent,
  numPresent,
  specialPresent,
  upperPresent,
} from "@/utilities/regex";
//Components

export default function ResetPassword({ searchParams }) {
  const { t } = useTranslation();
  const [oldPassword, setOldPassWord] = useState("");
  const [newPassword, setNewPassword] = useState("");
  const [passwordErrors, setPasswordErrors] = useState([]);
  const [oldPassError, setOldPassError] = useState("");
  const [newPassError, setNewPassError] = useState("");
  const [savebutton, setSaveButton] = useState(true);
  const [oldPassShow, setOldPassShow] = useState(false);
  const [newPassShow, setNewPassShow] = useState(false);
  const [overAllCheck, setOverAllCheck] = useState(false);
  const [loader, setLoader] = useState(false);
  const router = useRouter();
  let key = searchParams?.token;
  
  const validate = () => {
    let ValidateObj = {
      oldpass: true,
      newpass: true,
    };
    if (newPassword == "") {
      ValidateObj.newpass = false;
      setNewPassError("Enter your password");
    } else {
      let arrayValue = [];
      if (!upperPresent(newPassword)) {
        arrayValue.push("Must contain at least 1 in Capital Case");
      } else {
        arrayValue.filter(
          (data) => data == "Must contain at least 1 in Capital Case"
        );
      }

      if (!numPresent(newPassword)) {
        arrayValue.push("Must have at least 1 Number");
      } else {
        arrayValue.filter((data) => data == "Must have at least 1 Number");
      }
      if (!lowerPresent(newPassword)) {
        arrayValue.push("Must contain at least 1 Lower Case");
      } else {
        arrayValue.filter(
          (data) => data == "Must contain at least 1 Lower Case"
        );
      }
      if (!specialPresent(newPassword)) {
        arrayValue.push("Must contain at least 1 Special characters");
      } else {
        arrayValue.filter(
          (data) => data == "Must contain at least 1 Special characters"
        );
      }
      if (newPassword.length <= 5) {
        arrayValue.push("Must be at least 6 characters");
      } else {
        arrayValue.filter((data) => data == "Must be at least 6 characters");
      }
      if (arrayValue.length == 0) {
        ValidateObj.newpass = true;
        setNewPassError("");
      } else {
        ValidateObj.newpass = false;
        setNewPassError(
          "Incorrect Password. Please check the password you have entered."
        );
      }
    }
    if (oldPassword == "") {
      ValidateObj.oldpass = false;
      setOldPassError(t("validationMessage.passwordEmpty"));
    } else {
      let arrayValue = [];
      if (!upperPresent(oldPassword)) {
        arrayValue.push("Must contain at least 1 in Capital Case");
      } else {
        arrayValue.filter(
          (data) => data == "Must contain at least 1 in Capital Case"
        );
      }

      if (!numPresent(oldPassword)) {
        arrayValue.push("Must have at least 1 Number");
      } else {
        arrayValue.filter((data) => data == "Must have at least 1 Number");
      }
      if (!lowerPresent(oldPassword)) {
        arrayValue.push("Must contain at least 1 Lower Case");
      } else {
        arrayValue.filter(
          (data) => data == "Must contain at least 1 Lower Case"
        );
      }
      if (!specialPresent(oldPassword)) {
        arrayValue.push("Must contain at least 1 Special characters");
      } else {
        arrayValue.filter(
          (data) => data == "Must contain at least 1 Special characters"
        );
      }
      if (oldPassword.length <= 5) {
        arrayValue.push("Must be at least 6 characters");
      } else {
        arrayValue.filter((data) => data == "Must be at least 6 characters");
      }
      if (arrayValue.length == 0) {
        if (oldPassword == newPassword) {
          ValidateObj.oldpass = true;
          setOldPassError("");
        } else {
          ValidateObj.oldpass = false;
          setOldPassError(t("validationMessage.confirmPassword"));
        }
      } else {
        ValidateObj.oldpass = false;
        setOldPassError(t("validationMessage.worngPassword"));
      }
    }
    if (ValidateObj.newpass == true && ValidateObj.oldpass == true) {
      return true;
    } else {
      return false;
    }
  };
  useEffect(() => {
    if (newPassword == "") {
      setNewPassError(t("validationMessage.passwordEmpty"));
    } else {
      let arrayValue = [];
      if (!upperPresent(newPassword)) {
        arrayValue.push("Must contain at least 1 in Capital Case");
      } else {
        arrayValue.filter(
          (data) => data == "Must contain at least 1 in Capital Case"
        );
      }

      if (!numPresent(newPassword)) {
        arrayValue.push("Must have at least 1 Number");
      } else {
        arrayValue.filter((data) => data == "Must have at least 1 Number");
      }
      if (!lowerPresent(newPassword)) {
        arrayValue.push("Must contain at least 1 Lower Case");
      } else {
        arrayValue.filter(
          (data) => data == "Must contain at least 1 Lower Case"
        );
      }
      if (!specialPresent(newPassword)) {
        arrayValue.push("Must contain at least 1 Special characters");
      } else {
        arrayValue.filter(
          (data) => data == "Must contain at least 1 Special characters"
        );
      }
      if (newPassword.length <= 5) {
        arrayValue.push("Must be at least 6 characters");
      } else {
        arrayValue.filter((data) => data == "Must be at least 6 characters");
      }
      if (arrayValue.length == 0) {
        setNewPassError("");
      } else {
        setNewPassError(
          "Incorrect Password. Please check the password you have entered."
        );
      }
      setPasswordErrors(arrayValue);
    }
  }, [newPassword]);

  useEffect(() => {
    if (savebutton) {
      validate();
    }
    if (validate()) {
      setOverAllCheck(true);
    } else {
      setOverAllCheck(false);
    }
  }, [newPassword, oldPassword, savebutton]);
  const handleSave = () => {
    setLoader(true);
    setSaveButton(true);
    if (validate()) {
      ResetPasswordApi(newPassword, key, router, setLoader);
    }
  };
  return (
    <>
      <Suspense fallback={null} />
      <div className="mt-1 bg-blue-100">
        <div className="pt-12 pb-48 mx-auto max-w-screen-2xl">
          <div className="max-w-xl m-auto">
            <div className="flex flex-col p-10 bg-white">
              <div className="mb-14">
                <h3 className="mb-1 text-2xl font-black text-black-300">
                  {t("forgotpassword.RstPwd")}
                </h3>
                <p className="text-base font-normal text-black-300">
                  {t("forgotpassword.EntNewPwd")}
                </p>
              </div>
              <div className="w-full">
                <div className="max-w-full sm:max-w-lg">
                  <div
                    className="relative w-full mb-6"
                    data-twe-input-wrapper-init
                  >
                    <input
                      type={newPassShow ? "text" : "password"}
                      value={newPassword}
                      onChange={(e) =>
                        setNewPassword(e.target.value.trimStart())
                      }
                      className={`pe-[50px] peer block h-11 w-full rounded border border-grey-400 bg-transparent px-3 py-[0.32rem] leading-[1.6] outline-none transition-all duration-200 ease-linear focus:placeholder:opacity-100 text-black peer-focus:text-primary data-[twe-input-state-active]:placeholder:opacity-100 motion-reduce:transition-none dark:text-white dark:placeholder:text-grey-400 dark:autofill:shadow-autofill dark:peer-focus:text-primary [&:not([data-twe-input-placeholder-active])]:placeholder:opacity-0 focus:border-grey-400 focus:shadow-none focus:ring-transparent focus:border-grey-400  ${
                        savebutton &&
                        newPassword != "" &&
                        newPassError !== "" &&
                        "border-valid"
                      }`}
                      id="exampleFormControlInput1"
                      placeholder={t("signin.Examplelabel")}
                    />
                    <label
                      htmlFor="exampleFormControlInput1"
                      className={
                        newPassword == ""
                          ? `pointer-events-none absolute left-3 top-2  mb-0 max-w-[90%] origin-[0_0] truncate leading-[1.6] text-neutral-500 transition-all duration-200 ease-out peer-focus:-translate-y-[1.01rem] peer-focus:scale-[0.8] peer-focus:text-primary peer-data-[twe-input-state-active]:-translate-y-[0.8rem] peer-data-[twe-input-state-active]:scale-[0.8] motion-reduce:transition-none dark:text-neutral-400 dark:peer-focus:text-primary bg-blue-100 ${
                              savebutton == true &&
                              newPassword != "" &&
                              "text-red-invalid"
                            }`
                          : `translate-y-[-0.8rem] scale-[0.8] pointer-events-none absolute left-3 top-1 mb-0 max-w-[90%] origin-[0_0] truncate leading-[1.6] text-neutral-500 transition-all duration-200 ease-out peer-focus:-translate-y-[0.8rem] peer-focus:scale-[0.8] peer-focus:text-primary peer-data-[twe-input-state-active]:-translate-y-[0.9rem] peer-data-[twe-input-state-active]:scale-[0.8] motion-reduce:transition-none dark:text-neutral-400 dark:peer-focus:text-primary bg-blue-100 text-red-valid ${
                              savebutton == true &&
                              newPassword != "" &&
                              newPassError !== "" &&
                              "text-red-invalid"
                            }`
                      }
                    >
                      {t("forgotpassword.NewPwd")}
                    </label>
                    {newPassword !== "" && (
                      <div className="mt-2">
                        <ul className="flex flex-col space-y-4 list-disc gap-y-2 ps-7">
                          <li
                            className={`text-xs font-normal ${
                              passwordErrors.includes(
                                "Must contain at least 1 in Capital Case"
                              )
                                ? "text-grey-400"
                                : "text-activegreen"
                            }`}
                          >
                            {t("validationMessage.CapitalCase")}
                          </li>
                          <li
                            className={`text-xs font-normal ${
                              passwordErrors.includes(
                                "Must have at least 1 Number"
                              )
                                ? "text-grey-400"
                                : "text-activegreen"
                            }`}
                          >
                            {t("validationMessage.Number")}
                          </li>
                          <li
                            className={`text-xs font-normal ${
                              passwordErrors.includes(
                                "Must contain at least 1 Lower Case"
                              )
                                ? "text-grey-400"
                                : "text-activegreen"
                            }`}
                          >
                            {t("validationMessage.LowerCase")}
                          </li>
                          <li
                            className={`text-xs font-normal ${
                              passwordErrors.includes(
                                "Must contain at least 1 Special characters"
                              )
                                ? "text-grey-400"
                                : "text-activegreen"
                            }`}
                          >
                            {t("validationMessage.SpecChars")}
                          </li>
                          <li
                            className={`text-xs font-normal ${
                              passwordErrors.includes(
                                "Must be at least 6 characters"
                              )
                                ? "text-grey-400"
                                : "text-activegreen"
                            }`}
                          >
                            {t("validationMessage.Characters")}
                          </li>
                        </ul>
                      </div>
                    )}
                    <a
                      role="button"
                      className="absolute flex items-center space-x-3 text-sm font-medium cursor-pointer top-3 right-3 text-black-300"
                      onClick={() => setNewPassShow(!newPassShow)}
                    >
                      {newPassShow ? t("signin.Hide") : t("signin.Show")}
                    </a>
                  </div>
                  <div className="relative w-full" data-twe-input-wrapper-init>
                    <input
                      type={oldPassShow ? "text" : "password"}
                      value={oldPassword}
                      onChange={(e) =>
                        setOldPassWord(e.target.value.trimStart())
                      }
                      className={`pe-[50px] peer block h-11 w-full rounded border border-grey-400 bg-transparent px-3 py-[0.32rem] leading-[1.6] outline-none transition-all duration-200 ease-linear focus:placeholder:opacity-100 text-black peer-focus:text-primary data-[twe-input-state-active]:placeholder:opacity-100 motion-reduce:transition-none dark:text-white dark:placeholder:text-grey-400 dark:autofill:shadow-autofill dark:peer-focus:text-primary [&:not([data-twe-input-placeholder-active])]:placeholder:opacity-0 focus:border-grey-400 focus:shadow-none focus:ring-transparent focus:border-grey-400  ${
                        savebutton &&
                        oldPassword != "" &&
                        oldPassError !== "" &&
                        "border-valid"
                      }`}
                      id="exampleFormControlInput2"
                      placeholder={t("signin.Examplelabel")}
                    />
                    <label
                      htmlFor="exampleFormControlInput2"
                      className={
                        oldPassword == ""
                          ? `pointer-events-none absolute left-3 top-2  mb-0 max-w-[90%] origin-[0_0] truncate leading-[1.6] text-neutral-500 transition-all duration-200 ease-out peer-focus:-translate-y-[1.01rem] peer-focus:scale-[0.8] peer-focus:text-primary peer-data-[twe-input-state-active]:-translate-y-[0.8rem] peer-data-[twe-input-state-active]:scale-[0.8] motion-reduce:transition-none dark:text-neutral-400 dark:peer-focus:text-primary bg-blue-100 ${
                              savebutton == true &&
                              oldPassword != "" &&
                              "text-red-invalid"
                            }`
                          : `translate-y-[-0.8rem] scale-[0.8] pointer-events-none absolute left-3 top-1 mb-0 max-w-[90%] origin-[0_0] truncate leading-[1.6] text-neutral-500 transition-all duration-200 ease-out peer-focus:-translate-y-[0.8rem] peer-focus:scale-[0.8] peer-focus:text-primary peer-data-[twe-input-state-active]:-translate-y-[0.9rem] peer-data-[twe-input-state-active]:scale-[0.8] motion-reduce:transition-none dark:text-neutral-400 dark:peer-focus:text-primary bg-blue-100 text-red-valid ${
                              savebutton == true &&
                              oldPassword != "" &&
                              oldPassError !== "" &&
                              "text-red-invalid"
                            }`
                      }
                    >
                      {t("forgotpassword.CfmNewPwd")}
                    </label>
                    {savebutton && oldPassword != "" && oldPassError !== "" && (
                      <p className="mt-1 text-xs font-normal text-red-invalid">
                        {oldPassError}
                      </p>
                    )}
                    <a
                      role="button"
                      className="absolute flex items-center space-x-3 text-sm font-medium cursor-pointer top-3 right-3 text-black-300"
                      onClick={() => setOldPassShow(!oldPassShow)}
                    >
                      {oldPassShow ? t("signin.Hide") : t("signin.Show")}
                    </a>
                  </div>
                  <button
                    disabled={overAllCheck == false}
                    className={`bg-boldblack hover:bg-zinc-800 rounded text-white btn flex items-center justify-center text-base font-bold mt-8 w-full h-12 ${
                      overAllCheck == true ? "opacity-100" : "opacity-15"
                    }`}
                    onClick={handleSave}
                  >
                    {" "}
                    {loader ? (
                      <svg
                        aria-hidden="true"
                        role="status"
                        className="inline w-5 h-5 text-white me-3 animate-spin"
                        viewBox="0 0 100 101"
                        fill="none"
                        xmlns="http://www.w3.org/2000/svg"
                      >
                        <path
                          d="M100 50.5908C100 78.2051 77.6142 100.591 50 100.591C22.3858 100.591 0 78.2051 0 50.5908C0 22.9766 22.3858 0.59082 50 0.59082C77.6142 0.59082 100 22.9766 100 50.5908ZM9.08144 50.5908C9.08144 73.1895 27.4013 91.5094 50 91.5094C72.5987 91.5094 90.9186 73.1895 90.9186 50.5908C90.9186 27.9921 72.5987 9.67226 50 9.67226C27.4013 9.67226 9.08144 27.9921 9.08144 50.5908Z"
                          fill="#E5E7EB"
                        />
                        <path
                          d="M93.9676 39.0409C96.393 38.4038 97.8624 35.9116 97.0079 33.5539C95.2932 28.8227 92.871 24.3692 89.8167 20.348C85.8452 15.1192 80.8826 10.7238 75.2124 7.41289C69.5422 4.10194 63.2754 1.94025 56.7698 1.05124C51.7666 0.367541 46.6976 0.446843 41.7345 1.27873C39.2613 1.69328 37.813 4.19778 38.4501 6.62326C39.0873 9.04874 41.5694 10.4717 44.0505 10.1071C47.8511 9.54855 51.7191 9.52689 55.5402 10.0491C60.8642 10.7766 65.9928 12.5457 70.6331 15.2552C75.2735 17.9648 79.3347 21.5619 82.5849 25.841C84.9175 28.9121 86.7997 32.2913 88.1811 35.8758C89.083 38.2158 91.5421 39.6781 93.9676 39.0409Z"
                          fill="currentColor"
                        />
                      </svg>
                    ) : (
                      t("forgotpassword.RstMyPwd")
                    )}
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
